export default {
	data() {
		return {
			apiUrl: getApp().globalData.apiUrl,
			dataList: [],
		}
	},
	onLoad: function() {
		uni.request({
			url: "https://api.apiopen.top/searchMusic?name=泡沫",
			success: res => {
				console.log(res)
			}
		})
		uni.request({
			url: `${this.apiUrl}/mv/first?limit=5`,
			success: res => {
				let arr = res.data.data;
				Promise.all(
					arr.map(item => {
						return this.getMv(`/mv/url?id=${item.id}`);
					})
				).then(res => {
					this.dataList = arr.map(item => {
						res.forEach(items => {
							if (item.id == items.id) {
								item.mvUrl = items.url
							}
						})
						return item;
					})
				})
			}
		})
	},
	onReady: function(res) {
		this.videoContext = uni.createVideoContext('mVideo')
	},
	methods: {
		mvPlay: function() {
			console.log("播放")
			// console.log(this.videoContext.pause());
			
			// this.videoContext.play()
		},
		getMv: function(url) {
			return new Promise((resolve, reject) => {
				uni.request({
					url: this.apiUrl + url,
					success: res => {
						// this.dataList.map(item => {
						// 	item.mvUrl = res.data.data;
						// 	return item
						// })
						resolve(res.data.data)
					}
				})
			})
		}
	}
}
